.ns-box {
  right: 0;
}

.ns-box .ns-thumb {
  height: 50px;
  width: 50px;
  font-size: 25px;
  text-align: center;
}

.ns-box .ns-thumb i {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
}

.ns-type-success .ns-thumb {
  background-color: #34B5DC;
}

.ns-type-warning .ns-thumb {
  background-color: #FFC107;
}

.ns-type-error .ns-thumb {
  background-color: #CD4237;
}

.ns-box.topright {
  top: 0;
  right: 0;
}

.notification-shape {
  position: fixed;
  z-index: 1000;
}

.notification-shape svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.ns-effect-thumbslider {
  background: transparent;
}

.ns-effect-thumbslider .ns-close {
  top: 1.8em;
  right: 1.8em;
}

.ns-effect-thumbslider.ns-show .ns-box-inner {
  transition-timing-function: ease-in-out;
  -webkit-transition: width 400ms;
  -moz-transition: width 400ms;
  -ms-transition: width 400ms;
  -o-transition: width 400ms;
  transition: width 400ms;
}

.ns-effect-thumbslider.ns-show .ns-close,
.ns-effect-thumbslider.ns-hide .ns-close,
.ns-effect-thumbslider.ns-show .ns-content p,
.ns-effect-thumbslider.ns-hide .ns-content p {
  -webkit-animation-name: animFade;
  animation-name: animFade;
  -webkit-animation-duration: 0.3s;
  animation-duration: 0.3s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  opacity: 0;
}

.ns-effect-thumbslider.ns-show .ns-close,
.ns-effect-thumbslider.ns-show .ns-content p {
  -webkit-animation-delay: 0.8s;
  animation-delay: 0.8s;
}

.ns-effect-thumbslider.ns-hide .ns-close,
.ns-effect-thumbslider.ns-hide .ns-content p {
  -webkit-animation-direction: reverse;
  animation-direction: reverse;
}

.ns-effect-thumbslider .ns-close::before,
.ns-effect-thumbslider .ns-close::after {
  background: #ddd;
  width: 2px;
}

.ns-effect-thumbslider .ns-close:hover::before,
.ns-effect-thumbslider .ns-close:hover::after {
  background: #aaa;
}

.ns-effect-thumbslider .ns-box-inner {
  overflow: hidden;
}

.ns-effect-thumbslider .ns-thumb {
  position: absolute;
  z-index: 100;
  overflow: hidden;
}

.ns-effect-thumbslider .ns-content {
  background: #fff;
  color: #727275;
  padding: 0 40px 0 80px;
  height: 50px;
  line-height: 50px;
}

.ns-effect-thumbslider.ns-show .ns-content,
.ns-effect-thumbslider.ns-hide .ns-content {
  -webkit-animation-name: animSlide;
  animation-name: animSlide;
  -webkit-animation-duration: 0.4s;
  animation-duration: 0.4s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  animation-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
}

.ns-effect-thumbslider.ns-show .ns-content {
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
}

.ns-effect-thumbslider.ns-hide .ns-content {
  -webkit-animation-direction: reverse;
  animation-direction: reverse;
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}

@-webkit-keyframes animSlide {
  0% {
    -webkit-transform: translate3d(-100%, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
  }
}

@keyframes animSlide {
  0% {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.ns-effect-thumbslider.ns-show .ns-thumb,
.ns-effect-thumbslider.ns-hide .ns-thumb {
  -webkit-animation-name: animJelly;
  animation-name: animJelly;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.ns-effect-thumbslider.ns-hide .ns-thumb {
  -webkit-animation-direction: reverse;
  animation-direction: reverse;
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}

